import React from "react";
import { Alert } from "react-bootstrap";
import { useTranslation } from "react-i18next";

export default (args) => {
  /**
   * Default Properties
   */
  let defaults = {};

  /**
   * Properties
   */
  const props = Object.assign(defaults, args.data);

  // console.log("ALERT DATA", props);

  /**
   * Barrels
   */
  const { error, info } = props.barrelGetters;
  // const {} = props.barrelSetters;
  // const {} = props.barrelFunctions;
  const { onAlertClose: onClose } = props.barrelEventHandlers;

  // const autoClose = () => {
  //   window.setTimeout(() => {
  //     onClose();
  //   }, 5000);
  // };

  let t;
  const errorStrings = useTranslation("Errors").t;
  const infoStrings = useTranslation("Infos").t;
  if (error !== null && info !== null) {
    // ({ t } = useTranslation("Errors"));
    const msgError = errorStrings(error);
    // const msgError = useTranslation("Errors").t(error);
    // ({ t } = useTranslation("Infos"));
    const msgInfo = infoStrings(info);
    // const msgInfo = useTranslation("Errors").t(info);
    return (
      <>
        <Alert variant="danger" onClose={() => onClose("error")} dismissible>
          {msgError}
        </Alert>

        <Alert variant="info" onClose={() => onClose("info")} dismissible>
          {msgInfo}
        </Alert>
      </>
    );
  } else if (error !== null) {
    // ({ t } = useTranslation("Errors"));
    return (
      <Alert variant="danger" onClose={() => onClose()} dismissible>
        {errorStrings(error)}
      </Alert>
    );
  } else if (info !== null) {
    // ({ t } = useTranslation("Infos"));
    // autoClose();
    return (
      <Alert variant="info" onClose={() => onClose()} dismissible>
        {infoStrings(info)}
      </Alert>
    );
  }
  return <></>;

  // let ret = "";
  // if (error !== null) {
  //   let { t } = useTranslation("Errors");
  //   ret += Alert({variant: "danger", onClose: () => onClose()})
  //     <Alert variant="danger" onClose={() => onClose()} dismissible>
  //       {t(error)}
  //     </Alert>
  //   );
  // }

  // if (info !== null) {
  //   let { t } = useTranslation("Infos");
  //   ret += (
  //     <Alert variant="info" onClose={() => onClose()} dismissible>
  //       {t(info)}
  //     </Alert>
  //   );
  // }

  // if (ret !== "") return ret;
  // else return <></>;
};
